.header-color {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: #212121;
  transition: 0.3s;
  z-index: 55555;

  .logo-img {
    background: url(../../images/nike_logo_2.png) no-repeat left top/ 96%;
    width: 58px;
    height: 24px;
    text-indent: -9999em;
    display: block;
    margin-top: 8px;
    &::before {
      content: "";
      display: block;
      width: 100%;
      height: 100%;
    }
    .blind-s {
      position: absolute;
      clip: rect(0, 0, 0, 0);
      width: 1px;
      height: 1px;
      overflow: hidden;
    }
  }

  .gnb {
    > li {
      .color {
        color: #fff;
        &::after {
          display: none;
        }
      }
    }
  }
  .other {
    > li {
      &:nth-child(1) {
        margin-top: 6px;
        margin-right: 30px;
      }
      &:nth-child(2) {
        margin-top: 10px;
        margin-right: 10px;
      }
      &:nth-child(3) {
        margin-top: 10px;
        margin-right: 10px;
      }
      &:nth-child(4) {
        margin-top: 10px;
        margin-right: 10px;
      }
      &:nth-child(5) {
        margin-top: 3px;
        margin-right: 10px;
      }
      &:nth-last-child(1) {
        display: none;
      }
      > form {
        position: relative;
        > input[type="text"] {
          display: block;
          background-color: #f3f3f5;
          width: 160px;
          height: 34px;
          border: none;
          box-sizing: border-box;
          font-size: 1rem;
          border-radius: 8px;
          color: #9fa0a4;
          padding: 10px;
          padding-left: 37px;
          outline: none;
          &::placeholder {
            opacity: 0.5;
          }
        }
        .search_color {
          font-size: 20px;
          cursor: pointer;
          position: absolute;
          z-index: 1;
          top: 7px;
          left: 10px;
        }
      }

      .icon_color {
        color: #fff;
      }
      .login_color {
        color: #000;
        font-size: 14px;
        font-weight: 500;
        background: none;
        border: none;
        cursor: pointer;
        background-color: #fff;
        width: 70px;
        height: 32px;
        border-radius: 6px;
      }
      .logout_color {
        margin-top: 7px;
        color: #000;
        font-size: 16px;
        font-weight: 500;
        background: none;
        border: none;
        cursor: pointer;
        background-color: #fff;
        width: 70px;
        height: 34px;
        border-radius: 6px;
      }
      .mobile-menu-color {
        margin: 0;
        opacity: 0;
        display: none;
        > div {
          width: 21px;
          height: 3px;
          background: #fff;
          border-radius: 15px;
          &:nth-child(2) {
            margin: 3px 0;
          }
        }
      }
    }
  }
}

.header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 99999;
  transition: 0.5s;
  background-color: #fff;
  .b {
    display: block;
    width: 100%;
    height: 1px;
    background-color: #eaeae8;
    position: absolute;
    top: 79px;
    z-index: 1;
  }
}
.headerWrap {
  width: 1250px;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 3%;
  .logo {
    .logoLink {
      background: url(../../images/nike_logo.png) no-repeat left top/ 96%;
      width: 58px;
      height: 24px;
      text-indent: -9999em;
      display: block;
      margin-top: 8px;
      &::before {
        content: "";
        display: block;
        width: 100%;
        height: 100%;
      }
      .blind {
        position: absolute;
        clip: rect(0, 0, 0, 0);
        width: 1px;
        height: 1px;
        overflow: hidden;
      }
    }
  }
  .gnb {
    display: flex;
    align-items: center;
    flex: 1;

    > li {
      height: 80px;
      padding: 0 23px;
      box-sizing: border-box;
      text-align: center;
      font-family: "Roboto";

      &.on a::after {
        width: 100%;
      }
      &:nth-child(5).on a::after {
        display: none;
      }
      &:nth-child(6).on a::after {
        display: none;
      }
      &:nth-child(7).on a::after {
        display: none;
      }
      > a {
        font-size: 17px;
        font-weight: 500;
        line-height: 77px;
        transition: 0.2s;
        position: relative;
        display: block;
        width: 100%;
        height: 77px;
        padding-top: 5px;
        box-sizing: border-box;
        &::after {
          content: "";
          display: block;
          width: 0;
          height: 4px;
          background: #000;
          position: absolute;
          bottom: -4px;
          left: 0;
          transition: 0.4s;
          z-index: 1;
        }
      }
    }
  }

  .other {
    display: flex;
    align-items: center;
    > li {
      &:nth-child(1) {
        margin-top: 6px;
        margin-right: 30px;
      }
      &:nth-child(2) {
        margin-top: 10px;
        margin-right: 10px;
      }
      &:nth-child(3) {
        margin-top: 10px;
        margin-right: 10px;
      }
      &:nth-child(4) {
        margin-top: 10px;
        margin-right: 10px;
      }
      &:nth-last-child(2) {
        margin-right: 0;
        margin-top: 0;
      }
      &:nth-last-child(1) {
        display: none;
      }
      > form {
        position: relative;
        .search {
          font-size: 20px;
          cursor: pointer;
          position: absolute;
          z-index: 1;
          top: 7px;
          left: 10px;
        }
        > input[type="text"] {
          display: block;
          background-color: #f3f3f5;
          width: 180px;
          height: 34px;
          border: none;
          box-sizing: border-box;
          font-size: 1rem;
          border-radius: 8px;
          color: #9fa0a4;
          padding: 10px;
          padding-left: 37px;
          outline: none;
          &::placeholder {
            opacity: 0.5;
          }
        }
      }
      .m-searchWrap {
        display: none;
      }
      > a {
        font-size: 23px;
        display: block;
        .login {
          font-size: 14px;
          font-weight: 500;
          background: #000;
          border: none;
          cursor: pointer;
          width: 70px;
          height: 32px;
          border-radius: 6px;
          color: #fff;
        }
      }
      .logout {
        font-size: 14px;
        font-weight: 500;
        background: #000;
        border: none;
        cursor: pointer;
        width: 70px;
        height: 34px;
        border-radius: 6px;
        color: #fff;
        margin-top: 7px;
      }
    }
  }

  .subWrap {
    position: absolute;
    top: 80px;
    left: 0;
    right: 0;
    width: 100%;
    background: #fff;
    -webkit-box-shadow: 1px 11px 9px -9px rgba(91, 91, 91, 0.28);
    box-shadow: 1px 11px 9px -9px rgba(91, 91, 91, 0.28);
    transition: opacity 0.5s ease;
    opacity: 1;

    .sub {
      width: 1250px;
      margin: 0 auto;
      display: flex;
      padding-left: 119px;
      box-sizing: border-box;
      text-align: left;
      gap: 60px;
      justify-content: space-between;
      .subGnb {
        margin: 0;

        div {
          width: 220px;
          margin-top: -21px;

          a {
            img {
              width: 100%;
            }
          }
        }
        .title {
          margin-top: 40px;
          margin-bottom: 20px;
          > a {
            font-family: "Roboto";
            font-size: 16px;
            font-weight: 500;
            cursor: pointer;
          }
        }
        > ul {
          padding-bottom: 30px;
          .subcategoryLi {
            margin: 28px 0;
            a {
              font-family: "Noto Sans KR";
              color: #929292;
              cursor: pointer;
              font-size: 15px;
              font-weight: 500;
              display: block;
              transition: 0.3s;
              &:hover {
                color: #000;
              }
            }
          }
        }
      }
    }
  }
}

@media (max-width: 1200px) and (min-width: 768px) {
  .headerWrap {
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;

    .gnb {
      > li {
        padding: 0 1rem;
      }
    }
  }
}
@media (max-width: 1024px) and (min-width: 769px) {
  .header-color {
    .logo-img {
      width: 55px;
      height: 21px;
    }

    .other {
      > li {
        form {
          .search {
            color: #000;
          }
        }
      }
    }
  }
  .headerWrap {
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;
    .logo {
      .logoLink {
        width: 55px;
        height: 21px;
      }
    }
    .gnb {
      > li {
        &:nth-child(5),
        &:nth-child(6) {
          display: none;
        }
        padding: 0 0.6rem;
        > a {
          font-size: 1rem;
        }

        .subWrap {
          .sub {
            padding: 0 30px;
          }
        }
      }
    }
    .other {
      > li {
        &:nth-child(1) {
          margin-right: 10px;
        }
        > a {
          font-size: 1.5rem;
        }
        form {
          .search {
            font-size: 1.3rem;
            top: 6px;
            left: 7px;
          }
          > input[type="text"] {
            display: block;
          }
        }
      }
    }
    .mobileBg {
      display: none;
    }
    .mobileGnb {
      display: none;
    }
  }
}
