.banner-wrap {
  width: 100%;
  height: 547px;
  padding-top: 200px;
  box-sizing: border-box;
  > p {
    text-align: center;
    font-size: 1.4rem;
    font-weight: 400;
    font-family: "Noto Sans KR", sans-serif;
    position: relative;
    &::after {
      content: "";
      display: block;
      width: 1px;
      height: 90px;
      background: #000;
      position: absolute;
      left: 50%;
      top: 97px;
    }
  }
}
@media all and (max-width: 1024px) and (min-width: 768px) {
  .card-wrap {
    .card-txt {
      .bg_txt1 {
        font-size: 12.9vw !important;
        letter-spacing: 3vw;
      }
      > h3 {
        font-size: 6vw !important;
        margin-top: -1vw;
      }
    }
  }
}
@media all and (max-width: 768px) and (min-width: 425px) {
  .card-wrap {
    .card-txt {
      .bg_txt1 {
        font-size: 13vw !important;
        letter-spacing: 3vw !important;
      }
      > h3 {
        font-size: 8vw !important;
        margin-top: -3vw;
      }
    }
  }
}

@media all and (max-width: 425px) and (min-width: 320px) {
  .card-wrap {
    .card-txt {
      .bg_txt1 {
        font-size: 13vw !important;
        letter-spacing: 3vw !important;
      }
      > h3 {
        font-size: 8vw !important;
        margin-top: -3vw;
      }
    }
  }
}
